.home{
    
    width: 100%;
    height: 90vh;
    background:linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2)), url(../images/cloud-bg.png) no-repeat center center/cover;
   
}
.home .container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.home .content{
    height: 80vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0rem;
    margin-left: 0.5 rem;
    color: white;
}
.home .content h1{
    width: 100%;
    display: flex;
    font-weight: bold;
    font-size: 4rem;
    font-family: "Poppins", sans-serif;
}
@media screen and (max-width: 940px) {
    .home .content{
        display: flex;
        flex-direction: column;
    }
.home .content h1{
    width: 100%;
    font-size: 3.7rem;
    padding: 2rem 3rem;
} 
.home .content h5{

    padding: 0 2rem;
}


}

section{
    margin: 3rem ;

    
}
.c_photo{
    padding-left:  4rem;
    
}
.v_container{
    margin-top: 15rem;
}
.v_container h2{
    position:absolute;
    top: 55rem;
    font-weight: 800;
    font-size: 2.5rem;
    /* padding-left: 2rem; */
}
.bss__convert{
    color:transparent;
    margin-left:0;
    margin-bottom: 20px;
    padding-bottom: 0 ;
    background: linear-gradient(150deg, #2e8bd7,  #03059c); 
    height: 0.5rem; 
    align-items: center;
    width: 30%;
    border-radius: 5px;
}
.v_container h5{
    padding: 1rem 0;
    color: rgb(31, 31, 223);
    font-weight: 600;
}
.v_container h6{
    line-height: 30px;
    font-size: 1.2rem;
    font-weight: 500;
    padding-right:  3rem;
}
@media screen and (max-width: 940px) {
    .c_photo{
        padding-left: 0px;
    }
    .c_photo img{
        height: 60vh;
    }
    .v_container{
        margin-top: 3rem;
    }
    .v_container h2{
        position: relative;
        top: -1rem;
        font-weight: 800;
        font-size: 2.5rem;
        /* padding-left: 2rem; */
    }
    .image {
        display: none;
    }
}



.image {
    position: absolute;
    right: 4rem;
    top: 52rem;
   filter:invert(100%);
    width:250px;
    height:250px;
    margin:-60px 0 0 -60px;
    -webkit-animation:spin 40s linear infinite;
    -moz-animation:spin 40s linear infinite;
    animation:spin 40s linear infinite;
}
@-moz-keyframes spin { 
    100% { -moz-transform: rotate(360deg); } 
}
@-webkit-keyframes spin { 
    100% { -webkit-transform: rotate(360deg); } 
}
@keyframes spin { 
    100% { 
        -webkit-transform: rotate(360deg); 
        transform:rotate(360deg); 
    } 
}
