.navbar{
    width: 100%;
    padding: 0;
    margin: 0; 
    background-color: rgb(218, 216, 216);

}
.contain{
    margin: 0 ;
    padding: 0 ;
    background: linear-gradient(150deg, #369ff6,  #03059c); 
    height: 2.4rem; 
    align-items: center;
    width: 100%;

   
}
.contain .aaa{
    display: flex;

}
.aaa .bbb{
    margin-right: 25rem;
}
.aaa .bbb .add{
    display: flex;
}
.aaa .bbb .add li{
    justify-content: space-between;
    margin-top: 0.6rem;
    color: white;
    font-size: 15px;
    display: flex;
    margin-right: 10px;

}
.aaa .bbb .add li .iconk{
    margin-right: 5px;
    margin-top: 3px;
    font-size: 1rem;
}
.aaa .ccc .add{
    display: flex;
    float: right;
    margin-right: 6.1rem;
}
.aaa .ccc .add li{
    font-size: 1rem;
    color: white;
    margin-top: 5px;
}


/* navbar */
.navbar .container{
    display: flex;
    justify-content: space-between;
    align-items: center;

    

}
.navbar .container h2{
    
    display: flex;
    
    font-weight: 800;
    margin-left: -350px;


}
.navbar .container img{
    width: 3.5rem;
    height: auto;
    border-radius: 50%;
    
}
.navbar .nav-menu{
    display: flex;
    padding: 0.5rem 0;
}
.navbar li{

    padding-right: 1.3rem;
    font-weight: 600;
    justify-content: space-between;
    margin-top: 1rem;

    
}
.navbar .list_item{
    position: relative;
   
    
}


.navbar .list_item::after{
    content: '';
    position: absolute;
    left: 0;
    bottom: -0px ;
    width: 75%;
    height: 3px ;
    background: linear-gradient(150deg, #2e8bd7,  #03059c); 
    border-radius: 40px;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform .3s;
}

.navbar .list_item:hover::after{
    transform-origin: left;
    transform: scaleX(1);
}
.bar{
    display: none;
    /* padding: 1rem; */
}



@media screen and (max-width: 940px) {
    .navbar .container h2{
    
        display: flex;
        /* align-items: center; */
         font-weight: 800;
        margin-left: -200px;
        font-size: 1.9rem;
    
    
    }
    .navbar .container img{
        width: 3rem;
        height: auto;
        border-radius: 50%;
    }


    .navbar .bar{
        display: block;
        font-size: 20px;
    }

    .navbar .nav-menu{
        position: absolute;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        top: 50px;
        left:-100%;
        align-items: center;
        justify-content: center;
        background: whitesmoke;
        transition: 0.5s ease-in;
    }
    .navbar .active{
        left: 0;
    }
    .navbar li{
        color:white;
        padding: 1rem;
        /* font-size: 25px; */
        
    }
    .contain{
        display: none;
    }
    }